.App {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  font-family: sans-serif;
}

.choice-lists {
  width: 30%;
}

.choice-list {
  margin-bottom: 20px;
}

.choice {
  margin-bottom: 10px;
}

.choice h3 {
  margin: 0;
  padding: 5px 10px;
  border: 2px solid #2196f3;
  border-radius: 5px;
  color: #2196f3;
  cursor: pointer;
}

.choice h3:hover {
  background-color: #2196f3;
  color: #fff;
}

.options-lists {
  width: 40%;
  margin: 25px;

}

.selected-options {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  overflow: auto;
  /* Ajout de la propriété overflow */
  max-height: 90vh;
  /* Définition de la hauteur maximale */
}

.total {
  margin: 25px;
  padding: 5px 10px;
  border: 2px solid #212121;
  border-radius: 5px;
  color: #1d2226;
  cursor: pointer;
  text-align: center;
}


.selected-options {
  width: 30%;
}

.selected-options h2 {
  margin-top: 0;
}

.options-list.active {
  display: block;
}

.options {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.options h3 {
  margin: 0;
  padding: 5px 10px;
  border: 2px solid #2196f3;
  border-radius: 5px;
  color: #2196f3;
  cursor: pointer;
}

.options h3:hover {
  background-color: #2196f3;
  color: #fff;
}

.selected-options p {
  margin: 0;
  padding: 5px 10px;
  border: 2px solid #2196f3;
  border-radius: 5px;
  display: inline-block;
  margin-right: 10px;
}

.selected-options p span {
  font-weight: bold;
}

.selected-options p button {
  margin-left: 10px;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  background-color: #ff5252;
  color: #fff;
  cursor: pointer;
}

.selected-options p button:hover {
  background-color: #f00;
}

@media (max-width: 768px) {
  .App {
    flex-direction: column;
    align-items: center;
  }

  .choice-lists,
  .options-lists,
  .selected-options {
    width: 100%;
  }

  .options-list {
    margin-top: 20px;
  }
}

@media open {
  .no-print, .no-print * {
    display: none !important;
    visibility: hidden;
  }
  button {
    display: none !important;
    visibility: hidden;
  }
}